* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.container_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(60vh);
}

.btn_task_one {
  display: inline-block;
  margin-right: 10px;
  padding: 20px 50px;
  border-radius: 10px;
  outline: none;
  border: none;
}

.input_task_two {
  padding: 10px 20px;
}
.btn_task_two {
  padding: 10px 20px;
}

.timer {
  display: flex;
  margin-right: -20px;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
}

.value {
  font-size: 65px;
}

.label {
  font-size: 20px;
  text-transform: uppercase;
}
